import Vue from "vue";
import cameraActionsheet from "../../common/cameraActionsheet";
export default {
  data() {
    return {
      isShow: false,
      menus: {
        album: "",
        takePhoto: ""
      }
    };
  },
  methods: {
    checkCamera() {
      this.isShow = true;
    },
    cameraSuccess(url) {
      // 
      this.$vux.alert.show({
        title: "",
        content: url
      });
    },
    cameraError(message) {}
  },
  components: {
    cameraActionsheet
  }
};



